.main-header__logo {
  cursor: pointer;
  border: 2px solid transparent;
}
.main-header__logo:hover {
  opacity: 0.6;
}
.main-header__logo:active {
  opacity: 0.3;
}
.main-header__logo:focus {
  outline: 2px solid #444444;
}

.main-header__container-logo {
  position: relative;
  display: grid;
  grid-template-columns: auto 1fr 24px;
  align-items: center;
  justify-items: center;
  min-width: 280px;
  min-height: 70px;
}

.main-header__container-logo::after {
  position: absolute;
  bottom: 0;
  right: 0;
  left: -30px;
  content: "";
  height: 1px;
  width: calc(100vw + 30px);
  background-color: #e6e6e6;
  z-index: 3;
}

.main-header__logo-img {
  display: block;
  text-align: center;
}

.main-header__logo-letters {
  padding-right: 15px;
}

@media all and (min-width: 768px) {
